@import url(https://fonts.google.com/specimen/Open+Sans&display=swap);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


/* $minWidth: 320px; */

.grid-container {
  max-width: 1440px;
  display: grid;
  grid-template-areas: "header" "content";
  grid-template-columns: 1fr;
  margin: 0 auto;
  font-family: "Open+Sans";
  }

.header {
  height: 80px;
  grid-area: header;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  border-bottom: 0.1rem #e0e0e0 solid;
}

.checkList {
  display: -webkit-flex;
  display: flex;
  float: left;
  width: 270px;
  /* border-right: 0.1rem #e0e0e0 solid; */
}

.content{
  grid-area: content;
  display: -webkit-flex;
  display: flex;
  /* background-color: rgb(218, 245, 240); */
  direction: ltr;
  direction: initial;
  min-height: 600px;
}

.main {
  display: -webkit-flex;
  display: flex;
  /* background-color: rgb(223, 160, 160); */
  float: left;
}



.stepContainer {
        -webkit-flex: 1 1;
                flex: 1 1;
}

.pointContainer {
    --primary-color: #2f80ed;
    --secondary-color: #e0e0e0;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.point {
        min-width: 36px;
        width: 36px;
        height: 36px;
        border-radius: 50%;
        font-size: 26px;
        color: #998b8b;
        /* background-color: #2f80ed; */
        background-color: var(--secondary-color);
        border: thick double #998b8b;
    }

.pre {
        height: 1px;
        width: 100%;
        margin-top: 18px;
        background-color: var(--secondary-color);
    }
.post {
        height: 1px;
        width: 100%;
        margin-top: 18px;
        background-color: var(--secondary-color);
    }
.done {
        background-color: var(--primary-color);
        color: #e0e0e0;        
}
.inactive {
        background-color: var(--secondary-color);
}
.current{
        border: thick double var(--primary-color);
        color: #2f80ed;
}
.hide {
        visibility: hidden;
   }
.stepLabel {
        height: 1rem;
        min-width: 260px;
        text-align: center;
    }

/* The switch - the box around the slider */
.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }
  
  /* Hide default HTML checkbox */
  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }
  
  /* The slider */
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: #2196F3;
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px #2196F3;
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    transform: translateX(26px);
  }
  
  /* Rounded sliders */
  .slider.round {
    border-radius: 34px;
  }
  
  .slider.round:before {
    border-radius: 50%;
  }
  
